<template>
<div>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm"
    label-width="80px" class="demo-ruleForm">
  <el-form-item label="" prop="name">
    <el-input v-model="ruleForm.name" @change="setName"></el-input>
  </el-form-item>
    <el-form-item label="" prop="pass">
    <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item label="" prop="checkPass">
    <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
  </el-form-item>
  <el-form-item label="" prop="email">
    <el-input v-model="ruleForm.email"></el-input>
  </el-form-item>
  <el-form-item label="" prop="region">
    <el-col :span="12">
      <el-select v-model="ruleForm.region1" placeholder="">
        <el-option label="" value="shanghai"></el-option>
        <el-option label="" value="beijing"></el-option>
        <el-option label="" value="jiangsu"></el-option>
      </el-select>
    </el-col>
    <el-col :span="12">
      <el-select v-model="ruleForm.region2" placeholder="">
        <el-option label="" value="shanghai"></el-option>
        <el-option label="" value="beijing"></el-option>
      </el-select>
    </el-col>
  </el-form-item>
  <el-form-item label="" required>
    <el-col :span="24">
      <el-form-item prop="date1">
        <el-date-picker type="date" placeholder=""
          v-model="ruleForm.date1" style="width: 100%;">

        </el-date-picker>
      </el-form-item>
    </el-col>
  </el-form-item>
  <el-form-item label="" prop="gender">
    <el-radio-group v-model="ruleForm.gender">
      <el-radio label="1"></el-radio>
      <el-radio label="2"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="" prop="type">
    <el-checkbox-group v-model="ruleForm.type">
      <el-checkbox label="/" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
      <el-checkbox label="" name="type"></el-checkbox>
    </el-checkbox-group>
  </el-form-item>
  <el-form-item label="" prop="desc">
    <el-input type="textarea" v-model="ruleForm.desc"></el-input>
  </el-form-item>
</el-form>
</div>
</template>
<script>
export default {
  name: 'information',
  data() {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
      }
    };
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        name: '',
        pass: '',
        checkPass: '',
        email: '',
        region1: '',
        region2: '',
        date1: '',
        delivery: false,
        type: [],
        gender: '1',
        desc: '',
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 8,
            max: 16,
            message: ' 8  16 ',
            trigger: 'blur',
          },
        ],
        pass: [
          { required: true, validator: validatePass, trigger: 'blur' },
        ],
        checkPass: [
          { required: true, validator: validatePass2, trigger: 'blur' },
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: ['blur', 'change'] },
        ],
        region1: [
          { message: '', trigger: 'change' },
        ],
        region2: [
          { message: '', trigger: 'change' },
        ],
        date1: [
          {
            type: 'date', required: true, message: '', trigger: 'change',
          },
        ],
        type: [
          {
            type: 'array', required: true, message: '', trigger: 'change',
          },
        ],
        // desc: [
        //   { required: true, message: '', trigger: 'blur' },
        // ],
      },
    };
  },
  methods: {
    setName() {
      this.$emit('name', this.ruleForm.name);
    },
  },
};
</script>

