// Axios
import axios from 'axios';

const Axios = axios.create();
// 
Axios.interceptors.request.use(
  // token
  (config) => config,
  (err) => {
    Promise.reject(err);
  },
);
// 
Axios.interceptors.response.use(
  (config) => config,
  (err) => {
    Promise.reject(err);
  },
);
// get
export function get(url, params = {}) {
  return new Promise((resolve, reject) => {
    Axios({
      url,
      params,
      method: 'get',
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err) => {
        reject(err);
      });
  });
}
// post
export function post(url, params = {}, data = {}) {
  return new Promise((resolve, reject) => {
    Axios({
      url,
      method: 'post',
      params,
      data,
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err) => {
        reject(err);
      });
  });
}
// delete
export function del(url, params = {}, data = {}) {
  return new Promise((resolve, reject) => {
    Axios({
      url,
      method: 'delete',
      params,
      data,
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err) => {
        reject(err);
      });
  });
}
//   Blob,resopnseType:Blob
export function getBlob(url, params = {}) {
  return new Promise((resolve, reject) => {
    Axios({
      url,
      method: 'get',
      params,
      responseType: 'blob',
    })
      .then((res) => {
        resolve(res);
      })
      .catch((err) => {
        reject(err);
      });
  });
}

export default {
  get,
  post,
  del,
  getBlob,
};
